@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins',sans-serif;
}
.form{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 7%;
  padding-bottom: 7%;

}
.contaiiner{
  max-width: 800px;
  width: 100%;
  background-color: #fff;
  padding: 25px 30px;
  border-radius: 20px;
  box-shadow: 0 5px 10px rgba(0,0,0,0.15);
}
.contaiiner > .title{
  font-size: 20px;
  font-weight: 500;
  position: relative;
  letter-spacing: 3px;
}
.contaiiner > .title::before{
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 3px;
  width: 30px;
  border-radius: 5px;
  background: linear-gradient(135deg, #71b7e6, #9b59b6);
}
.content > form > .user-details{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 0 12px 0;
}
form > .user-details > .input-box{
  margin-bottom: 15px;
  width: calc(100% / 2 - 20px);
}
form > .input-box > span.details{
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}
.input-box > textarea  {
  width: 214%;
  height: 100%;
  outline: none;
  padding: 15px;
  border-radius: 15px;
}
@media (max-width:600px) {
  .input-box > textarea  {
    width: 26em;
    height: 5em;
    
  }
}
.user-details > .input-box > input{
  height: 45px;
  width: 100%;
  outline: none;
  font-size: 16px;
  border-radius: 15px;
  padding-left: 15px;
  border: 1px solid #ccc;
  border-bottom-width: 2px;
  transition: all 0.3s ease;
}
.user-details > .input-box > input:focus,
.user-details > .input-box > input:valid{
  border-color: #9b59b6;
}
 form > .gender-details > .gender-title{
  font-size: 20px;
  font-weight: 500;
 }
 form > .category{
   display: flex;
   width: 80%;
   margin: 14px 0 ;
   justify-content: space-between;
 }
 form > .category > label{
   display: flex;
   align-items: center;
   cursor: pointer;
 }
 form > .category > label > .dot{
  height: 18px;
  width: 18px;
  border-radius: 50%;
  margin-right: 10px;
  background: #d9d9d9;
  border: 5px solid transparent;
  transition: all 0.3s ease;
}
 
 form > .button{
   height: 45px;
   margin: 35px 0
 }
 form > .button >  input{
   height: 100%;
   width: 25%;
   text-align: center;
   align-items: center;
   border-radius: 15px;
   border: none;
   color: #fff;
   font-size: 18px;
   font-weight: 500;
   letter-spacing: 1px;
   cursor: pointer;
   transition: all 0.3s ease;
   background: linear-gradient(135deg, #71b7e6, #9b59b6);
 }
 .button {
   display: flex;
   justify-content: center;
   align-items: center;
   text-align: center;
 }
 form > .button > input:hover{
   transform: scale(0.99); 
   background: linear-gradient(-135deg, #71b7e6, #9b59b6);
  }
 @media(max-width: 584px){
 .container{
  max-width: 100%;
}
form > .user-details > .input-box{
    margin-bottom: 15px;
    width: 100%;
  }
  form > .category{
    width: 100%;
  }
  .content > form > .user-details{
    max-height: 300px;
    overflow-y: scroll;
  }
  .user-details::-webkit-scrollbar{
    width: 5px;
  }
  }
  @media(max-width: 459px){
  .container > .content > .category{
    flex-direction: column;
  }
}.nkarnerr {
    margin-left: 10%;
  
  }
  @media (max-width:600px) {
    .nkarnerr {
      display: none;
    }
  }
  .iimg {
    border-radius: 10%;
    padding-left: 5px;
    filter: drop-shadow(5px 5px 35px rgba(0, 0, 0, 0.25));
    margin-left: 15%;
    padding-bottom: 10%;
  }
.contact-map {
    width: 50%;
    height: 54%;
    border: 0;
    border-radius: 10%;
    filter: drop-shadow(5px 5px 35px rgba(0, 0, 0, 0.25));
    /* margin-left: 15%; */
}
